
.gpt3__navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
    
}

.gpt3__navbar-links_logo img {
    width: 62px;
    height: 16px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p ,
.gpt3__navbar-menu_container-links p,
.gpt3__navbar-menu_container-sign p{
    color: #fff;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container-sign button {
    color: #fff;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    text-transform: capitalize;

    padding: 0.5rem 1.5rem;
    cursor: pointer;

    border: 0;
    background: #FF4820;
    border-radius: 5px;
}

.gpt3__navbar-menu {
    display: none;
    margin-left: 1rem;
    position: relative;
}

.gpt3__navbar-menu_container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;

    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);

    text-align: end;
    background: var(--color-footer);
}

.gpt3__navbar-menu_container p {
    margin: 1rem;
}
.gpt3__navbar-menu_container-sign {
    display: none;
}


@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
   
}
@media screen and (max-width: 550px) {
    .gpt3__navbar {
        padding: 2rem;
    }
    .gpt3__navbar p {
        font-size: 16px;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-sign {
        display: block;
        margin-bottom: 1rem;
    }
   
}
